body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  --all-color: rgba(2, 90, 2, 0.8);
  --time: 6s;
}

html,
body {
  color: #3a3a3a;
  font-family: 'Montserrat', sans-serif;
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  width: 100%;
}

a {
  color: #3a3a3a;
  text-decoration: none;
}

.wrapper {
  margin: 0 auto;
  width: 85%;
}

.animate {
  -webkit-animation: salta 0.8s cubic-bezier(0.5,1.18,.8,1.1) forwards;
          animation: salta 0.8s cubic-bezier(0.5,1.18,.8,1.1) forwards;
}

/*NAVBAR*/
.navbar {
  -webkit-animation: 2s fadeIn forwards;
          animation: 2s fadeIn forwards;
  opacity: 0;
}

.navbar__section {
  display: flex;
  padding: 20px 0;
}

.navbar__item {
  flex: 1 1;
  font-size: 1em;
  text-align: center;
}

.navbar__link:hover {
  color: rgba(2, 90, 2, 0.8);
  color: var(--all-color);
}

@media (min-width: 385px) {
  .navbar__item {
    font-size: 1.5em;
  }
}

@-webkit-keyframes fadeIn {
  10% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  10% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes bounceInDown {
  0% {
     opacity: 0;
     -webkit-transform: translateY(-2000px);
             transform: translateY(-2000px);
  }
  60% {
     opacity: 1;
     -webkit-transform: translateY(30px);
             transform: translateY(30px);
  }
  80% {
     -webkit-transform: translateY(-10px);
             transform: translateY(-10px);
  }
  100% {
     -webkit-transform: translateY(0);
             transform: translateY(0);
  }
}

@keyframes bounceInDown {
  0% {
     opacity: 0;
     -webkit-transform: translateY(-2000px);
             transform: translateY(-2000px);
  }
  60% {
     opacity: 1;
     -webkit-transform: translateY(30px);
             transform: translateY(30px);
  }
  80% {
     -webkit-transform: translateY(-10px);
             transform: translateY(-10px);
  }
  100% {
     -webkit-transform: translateY(0);
             transform: translateY(0);
  }
}

@-webkit-keyframes salta {
  0%{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50%{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  80%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100%{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes salta {
  0%{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50%{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  80%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  100%{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.projects__title {
  -webkit-animation: bounceInDown 3s forwards;
          animation: bounceInDown 3s forwards;
  margin: 40px auto;
  text-align: center;
  width: 100%;
}

.title {
  position: relative;
}

.title::after {
  bottom: -5px;
  background-color:  var(--all-color);
  content: '';
  height: 3px;
  left: 50%;
  position: absolute;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 120px;
}

.projects__content {
  -webkit-animation: 3s fadeIn 2s forwards;
          animation: 3s fadeIn 2s forwards;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  opacity: 0;
  padding-top: 30px;
}
.content__card {
  margin-bottom: 60px;
  padding: 0 10px;
  text-align: center;
  width: 100%;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.card__meta {
  font-size: 14px;
}

.card__link {
  color: var(--all-color);
  display: block;
  margin: 16px 0;
  text-decoration: underline;
}

.content__card:hover {
  border-radius: 10px;
  box-shadow: 5px 8px 10px #3a3a3a;
  background-color: var(--all-color);
  color:white;
}

.content__card:hover .card__link {
  color: white;
  font-weight: bold;
}

.card__icons {
  font-size:30px;
}

.card__icons i {
  margin: 0 10px 16px;
}

@media (min-width: 550px) {
  .content__card {
    width: 50%;
  }
}

@media (min-width: 700px) {
  .content__card {
    width: 30%;
  }
}

.section__presentation {
  -webkit-animation:bounceInDown 4s forwards;
          animation:bounceInDown 4s forwards;
  align-content: center;
  align-items: center;
  display:flex;
  font-size: 1.5em;
  flex-direction: column;
  justify-content: center;
  position: relative;
  position: absolute;
  top: 25%;
  text-align: center;
  width: 100%;
}

.presentation__title {
  -webkit-animation: salta 1s cubic-bezier(0.5,1.18,.8,1.1) 4s forwards;
          animation: salta 1s cubic-bezier(0.5,1.18,.8,1.1) 4s forwards;
  text-shadow: 0px 2px 0px rgb(255, 255, 255);
}

.presentation__title--span {
  display: block;
}

.presentation__paragraph {
  font-size: 0.8em;
  height: auto;
  margin:0;
  padding: 20px;
  text-align: center;
  transition: color 250ms, text-shadow 250ms;
  width: auto;
}

.presentation__paragraph::after {
  bottom: -5px;
  background-color:  var(--all-color);
  content: '';
  height: 3px;
  left: 50%;
  position: absolute;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: all 250ms;
  width: 150px;
  z-index: -1;
}

.presentation__paragraph:hover {
  color: white;
  text-shadow: 0px 2px 0px rgba(0, 0, 0, 1);
}

.presentation__paragraph:hover::after {
    height: 105%;
    width: 105%;
}



@media (min-width: 380px) {
  .section__presentation{
    font-size: 2em;
  }

  .presentation__paragraph::after {
    width: 250px;
  }
}

@media (min-width: 500px) {
  .section__presentation{
    font-size: 2.5em;
  }

  .presentation__paragraph::after {
    width: 500px;
  }
}

@media (min-height: 1000px) {
  .section__presentation{
    font-size: 3em;
    top: 25%;
  }

  .presentation__paragraph::after {
    font-size: 1.3em;
    width: 650px;
  }
}

.section__social {
  display: flex;
  font-size: 2em;
  justify-content: space-evenly;
  width: 100%;
}

.social__link:hover {
  transition: 0.9s;
  -webkit-transform: rotateY(360deg);
          transform: rotateY(360deg);
}

@media (min-width: 380px) {
  .section__social {
  font-size: 2.5em;
  }
}

